@use "@wordpress/base-styles/mixins" as m;
@use "@wordpress/base-styles/variables" as v;
@use "@wordpress/base-styles/colors" as c;
/**
 * Editor only CSS.
 */

// Undo default editor styles.
// These need extra specificity.
.editor-styles-wrapper .sagiriswd-tn {
	// Extra specificity is applied to do two things in classic themes:
	// 1. Override the auto margin from alignment styles. This is needed as the `ul` element
	//    has a `wp-block` class applied to it, even though it's not a block.
	// 2. Override list indentation that targets `ul,ol` elements.
	ul {
		margin-top: 0;
		margin-bottom: 0;
		margin-left: 0;
		padding-left: 0;
	}

	// Revert any margins.
	// This also makes it work with classic theme auto margins.
	.sagiriswd-tn-item.wp-block {
		margin: revert;
	}
}

// The following rule affects the positioning of the dropdown arrow indicator.
// On the frontend, this element is inline, which makes it look correct. In the editor,
// the label is block, which causes the dropdown indicator to wrap onto a new line.
// Therefore we explicitly set it to inline even in the block editor.
.sagiriswd-tn-item__label {
	display: inline;
}


/**
 * Submenus.
 */

// Low specificity default to ensure background color applies to submenus.
.sagiriswd-tn__container,
.sagiriswd-tn-item {
	background-color: inherit;
}

// Only show the flyout on hover if the parent menu item is selected.
.sagiriswd-tn:not(.is-selected):not(.has-child-selected) .has-child:hover {
	> .sagiriswd-tn__submenu-container {
		opacity: 0;
		visibility: hidden;
	}
}

// Styles for submenu flyout.
.has-child {

	&.is-selected,
	&.has-child-selected {
		> .sagiriswd-tn__submenu-container {
			display: flex;
			opacity: 1;
			visibility: visible;
		}
	}
}

// Show a submenu when generally dragging (is-dragging-components-draggable) if that
// submenu has children (has-child) and is being dragged within (is-dragging-within).
.is-dragging-components-draggable .has-child.is-dragging-within {
	> .sagiriswd-tn__submenu-container {
		opacity: 1;
		visibility: visible;
	}
}

// IE fix for submenu visibility on parent focus.
.is-editing > .sagiriswd-tn__container {
	visibility: visible;
	opacity: 1;
	display: flex;
	flex-direction: column;
}

.is-dragging-components-draggable .sagiriswd-tn-link > .sagiriswd-tn__container {
	// Set opacity to 1 to still be able to show the draggable chip.
	opacity: 1;
	visibility: hidden;

	// Show the chip but hide the submenu list.
	.block-editor-block-draggable-chip-wrapper {
		visibility: visible;
	}
}

// Show even when a child is selected. This is an edgecase just for navigation submenus.
.is-editing > .sagiriswd-tn__submenu-container > .block-list-appender {
	display: block;
	position: static;
	width: 100%;

	// Colors need to work in both dark themes, and dark dropdown colors.
	// This customization would be nice to retire in a refactor of the canvas appender button.
	.block-editor-button-block-appender {
		color: c.$white;
		background: c.$gray-900;
		padding: 0;
		width: v.$button-size-small;
		margin-right: 0;
		margin-left: auto;
	}
}

// Hide when hovering.
.sagiriswd-tn__submenu-container .block-list-appender {
	display: none;
}

/**
 * Colors Selector component
 */

$colors-selector-size: 22px;

.block-library-colors-selector {
	width: auto;

	// Toolbar colors-selector button.
	.block-library-colors-selector__toggle {
		display: block;
		margin: 0 auto;
		padding: 3px;
		width: auto;
	}

	// Button container.
	.block-library-colors-selector__icon-container {
		height: 30px;
		position: relative;
		margin: 0 auto;
		padding: 3px;
		display: flex;
		align-items: center;
		border-radius: 4px;
	}

	.block-library-colors-selector__state-selection {
		margin-left: auto;
		margin-right: auto;

		// colors-selector - selection status.
		border-radius: $colors-selector-size * 0.5;
		box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.2);

		width: $colors-selector-size;
		min-width: $colors-selector-size;
		height: $colors-selector-size;
		min-height: $colors-selector-size;
		line-height: ($colors-selector-size - 2);
		padding: 2px;

		> svg {
			// Override `min-width: $button-size-small` on toolbar-group/style.scss
			min-width: auto !important;
		}

		// Styling icon color.
		&.has-text-color {

			> svg,
			> svg path {
				color: inherit;
			}
		}
	}
}

// Colors Selector Popover.
$color-control-label-height: 20px;

.block-library-colors-selector__popover {
	.color-palette-controller-container {
		padding: 16px;
	}

	.components-base-control__label {
		height: $color-control-label-height;
		line-height: $color-control-label-height;
	}

	.component-color-indicator {
		float: right;
		margin-top: 2px;
	}

	.components-panel__body-title {
		display: none;
	}
}

// Override inner padding on default appender.
// This should be a temporary fix, to be replaced by improvements to
// the sibling inserter. Or by the dropdown appender that is used in Group, instead of the "Button block appender".
// See https://github.com/WordPress/gutenberg/issues/37572.
.sagiriswd-tn .wp-block + .block-list-appender .block-editor-button-block-appender {
	background-color: c.$gray-900;
	color: c.$white;
	height: v.$button-size-small;

	// This needs specificity to override an inherited padding.
	// That source padding in turn has high specificity to protect
	// from theme CSS bleed.
	&.block-editor-button-block-appender.block-editor-button-block-appender {
		padding: 0;
	}
}

.sagiriswd-tn .wp-block .wp-block .block-editor-button-block-appender {
	background-color: transparent;
	color: c.$gray-900;
}

// Unstyle some inherited placeholder component styles.
.components-placeholder.sagiriswd-tn-placeholder {
	outline: none;
	padding: 0;
	box-shadow: none;
	background: none;
	min-height: 0;

	// Needed for the preview menu items to match actual menu items.
	.components-placeholder__fieldset {
		font-size: inherit;
	}

	.components-placeholder__fieldset .components-button {
		margin-bottom: 0;
	}

	// For the placeholder indicators to colorize correctly, colors need to be inherited unless selected.
	color: inherit;

	.sagiriswd-tn.is-selected & {
		color: c.$gray-900;
	}
}

// Unselected state.
.sagiriswd-tn-placeholder__preview {
	display: flex;
	align-items: center;
	min-width: v.$grid-unit-10 * 12;
	font-size: v.$default-font-size;
	font-family: v.$default-font;

	.sagiriswd-tn.is-selected & {
		display: none;
	}

	// Draw the dashed outline.
	// By setting the dashed border to currentColor, we ensure it's visible
	// against any background color.
	color: currentColor;
	background: transparent;

	&::before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		pointer-events: none;
		border: v.$border-width dashed currentColor;

		// Inherit border radius from style variations.
		border-radius: inherit;
	}

	> svg {
		fill: currentColor;
	}
}

// Prevent layout shift for vertical variant.
.sagiriswd-tn.is-vertical .is-small,
.sagiriswd-tn.is-vertical .is-medium {
	.components-placeholder__fieldset {
		// The size of two buttons, with top and bottom padding, and gap.
		min-height: (v.$button-size * 2) + v.$grid-unit-15 + (v.$grid-unit-15 * 0.5);
	}
}

.sagiriswd-tn.is-vertical .is-large {
	.components-placeholder__fieldset {
		// The size of three buttons, with top and bottom padding, and gap.
		min-height: (v.$button-size * 3) + v.$grid-unit-15 + v.$grid-unit-15;
	}
}

// Selected state.
.sagiriswd-tn-placeholder__preview,
.sagiriswd-tn-placeholder__controls {
	padding: (v.$grid-unit-15 * 0.5) v.$grid-unit-10;
	flex-direction: row;
	align-items: flex-start;
}

.sagiriswd-tn-placeholder__controls {
	border-radius: v.$radius-small;
	background-color: c.$white;
	box-shadow: inset 0 0 0 v.$border-width c.$gray-900;
	display: none;
	position: relative;
	z-index: 1;

	// Show when selected.
	.sagiriswd-tn.is-selected & {
		display: flex;
	}

	// If an ancestor has a text-decoration property applied, it is inherited regardless of
	// the specificity of a child element. Only pulling the child out of the flow fixes it.
	// See also https://www.w3.org/TR/CSS21/text.html#propdef-text-decoration.
	float: left;
	width: 100%;

	// Hide the navigation indicator when in small contexts.
	.is-small &,
	.is-medium & {

		.sagiriswd-tn-placeholder__actions__indicator,
		.sagiriswd-tn-placeholder__actions__indicator + hr {
			display: none;
		}
	}

	// Vertical orientation or small context.
	.is-small &,
	.sagiriswd-tn.is-vertical & {
		.sagiriswd-tn-placeholder__actions {
			flex-direction: column;
			align-items: flex-start;

			hr {
				display: none;
			}
		}
	}

	.sagiriswd-tn-placeholder__icon {
		margin-right: v.$grid-unit-15;
		height: v.$button-size; // Prevents jumpiness.
	}
}

// Block title
.sagiriswd-tn-placeholder__actions__indicator {
	display: flex;
	padding: 0 (v.$grid-unit-15 * 0.5) 0 0;
	align-items: center;
	justify-content: flex-start;
	line-height: 0;
	height: v.$button-size;

	// Line up with the icon in the toolbar.
	margin-left: v.$grid-unit-05;

	svg {
		margin-right: v.$grid-unit-05;
		fill: currentColor;
	}
}

// Keep as row for medium.
.sagiriswd-tn .components-placeholder.is-medium .components-placeholder__fieldset {
	flex-direction: row !important;
}

.sagiriswd-tn-placeholder__actions {
	display: flex;
	font-size: v.$default-font-size;
	font-family: v.$default-font;
	gap: v.$grid-unit-15 * 0.5;
	align-items: center;

	// Margins.
	.components-dropdown,
	> .components-button {
		margin-right: 0;
	}

	// Separator.
	height: 100%; // This allows the separator to scale vertically.

	// Separator.
	&.sagiriswd-tn-placeholder__actions hr {
		border: 0;
		min-height: v.$border-width;
		min-width: v.$border-width;
		background-color: c.$gray-900;
		margin: auto 0;
		height: 100%;
		max-height: v.$grid-unit-20;
	}
}


/**
 * Mobile menu.
 */

// These needs extra specificity in the editor.
.sagiriswd-tn__responsive-container:not(.is-menu-open) {
	.components-button.sagiriswd-tn__responsive-container-close {
		@include m.break-small {
			display: none;
		}
	}
}

// Emulate the fullscreen editing inside the editor.
// Most of this can be removed when the iframe lands.

// When not fullscreen.
.sagiriswd-tn__responsive-container.is-menu-open {
	position: fixed;
	top: v.$admin-bar-height-big + v.$header-height + v.$block-toolbar-height + v.$border-width;

	@include m.break-medium() {
		top: v.$admin-bar-height + v.$header-height + v.$border-width;
	}

	// Navigation sidebar rules.
	@include m.break-medium() {
		left: v.$admin-sidebar-width-collapsed;
	}

	@include m.break-large() {
		left: v.$admin-sidebar-width;
	}
}

.is-mobile-preview .sagiriswd-tn__responsive-container.is-menu-open,
.is-tablet-preview .sagiriswd-tn__responsive-container.is-menu-open {
	top: v.$admin-bar-height + v.$header-height + v.$block-toolbar-height + v.$border-width;
}

// When fullscreen.
.is-fullscreen-mode {
	.sagiriswd-tn__responsive-container.is-menu-open {
		left: 0; // Unset the value from non fullscreen mode.
		top: v.$admin-bar-height-big + v.$header-height + v.$block-toolbar-height + v.$border-width;

		@include m.break-medium() {
			top: v.$header-height + v.$border-width;
		}
	}

	.is-mobile-preview .sagiriswd-tn__responsive-container.is-menu-open,
	.is-tablet-preview .sagiriswd-tn__responsive-container.is-menu-open {
		top: v.$header-height + v.$block-toolbar-height + v.$border-width;
	}
}

// `body.editor-styles-wrapper` ensures this only applies to the iframed editor.
body.editor-styles-wrapper .sagiriswd-tn__responsive-container.is-menu-open {
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}

// The menu and close buttons need higher specificity in the editor.
.components-button.sagiriswd-tn__responsive-container-open.sagiriswd-tn__responsive-container-open,
.components-button.sagiriswd-tn__responsive-container-close.sagiriswd-tn__responsive-container-close {
	padding: 0;
	height: auto;
	color: inherit;
}

.components-heading.sagiriswd-tn-off-canvas-editor__title {
	margin: 0;
}

.sagiriswd-tn-off-canvas-editor__header {
	margin-bottom: v.$grid-unit-10;
}

// Customize the mobile editing.
// This can be revisited in the future, but for now, inherit design from the parent.
.is-menu-open .sagiriswd-tn__responsive-container-content * {
	.block-list-appender {
		margin-top: v.$grid-unit-20;
	}
}

// Space spinner to give it breathing
// room when block is selected and has focus outline.
.sagiriswd-tn__loading-indicator-container {
	padding: v.$grid-unit-10 v.$grid-unit-15;
}

.sagiriswd-tn .sagiriswd-tn__uncontrolled-inner-blocks-loading-indicator {
	margin-top: 0;
}

.sagiriswd-tn-delete-menu-button {
	width: 100%;
	justify-content: center;
	margin-bottom: v.$grid-unit-20;
}

// increased specificity to override button variant
// for the manage menus button in the advanced area
// of the navigation block
.components-button.is-link.sagiriswd-tn-manage-menus-button {
	margin-bottom: v.$grid-unit-20;
}

.sagiriswd-tn__overlay-menu-preview {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	background-color: c.$gray-100;
	padding: 0 v.$grid-unit-30;

	// Adding !important to override default 40px size.
	// Ref - https://github.com/WordPress/gutenberg/pull/65075#discussion_r1746282734
	height: v.$grid-unit-40 * 2 !important;

	grid-column: span 2;

	&.open {
		box-shadow: inset 0 0 0 v.$border-width c.$gray-200;
		outline: 1px solid transparent; // Box shadow is removed in high contrast mode, a transparent outline is made opaque.
		background-color: c.$white;
	}
}

// Temp - hide toolbar component when Nav Menu Selector is empty.
// TODO - extract state to common hook or parent component to avoid render.
.sagiriswd-tn__toolbar-menu-selector.components-toolbar-group:empty {
	display: none;
}

// Temp - hide adjacent <hr>s when Nav Menu Selector is empty.
// TODO - extract state to common hook or parent component to avoid render of <hr>.
.sagiriswd-tn-placeholder__actions hr + hr {
	display: none;
}

/**
 * Navigation selector styles
 */
.sagiriswd-tn__navigation-selector {
	margin-bottom: v.$grid-unit-20;
	width: 100%;
}

.sagiriswd-tn__navigation-selector-button {
	border: 1px solid;
	justify-content: space-between;
	width: 100%;
}

.sagiriswd-tn__navigation-selector-button__icon {
	flex: 0 0 auto;
}

.sagiriswd-tn__navigation-selector-button__label {
	flex: 0 1 auto;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.sagiriswd-tn__navigation-selector-button--createnew {
	border: 1px solid;
	margin-bottom: v.$grid-unit-20;
	width: 100%;
}

// Buttons inside a disabled component get semitransparent when there's a clickthrough overlay.
// Since this navigation button is content rather than UI, however, override that.
.sagiriswd-tn__responsive-container-open.components-button {
	opacity: 1;
}

.sagiriswd-tn__menu-inspector-controls {
	overflow-x: auto;

	@include m.custom-scrollbars-on-hover(transparent, c.$gray-600);
}

.sagiriswd-tn__menu-inspector-controls__empty-message {
	margin-left: 24px;
}

.editor-sidebar__panel .sagiriswd-tn__submenu-header {
	margin-top: 0;
	margin-bottom: 0;
}

.sagiriswd-tn__submenu-accessibility-notice {
	grid-column: span 2;
}

